.page-container.awareness {
  background: #f3e5f5;
  padding: 30px 35px;
  border-radius: 10px;
  font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif;
  box-shadow: 0 0 14px #ba68c8;
  max-width: 720px;
  margin: 30px auto;
}

.page-container.awareness h1 {
  color: #6a1b9a;
  margin-bottom: 20px;
  font-weight: 700;
}

.page-container.awareness p {
  font-size: 18px;
  margin-bottom: 15px;
  color: #4a148c;
}

.page-container.awareness ul {
  padding-left: 18px;
  font-size: 18px;
  color: #7b1fa2;
  line-height: 1.6;
  list-style-type: '➤ ';
}

.page-container.awareness ul li {
  margin-bottom: 12px;
  position: relative;
  padding-left: 25px;
}

.page-container.awareness ul li::before {
  content: "➤";
  position: absolute;
  left: 0;
  color: #9c27b0;
  font-weight: 700;
}

.page-container.awareness .extra-point {
  font-weight: 700;
  color: #8e24aa;
  margin-right: 6px;
}
