.page-container.student-schedule {
  background: #fff3e0;
  padding: 30px 35px;
  border-radius: 10px;
  font-family: 'Open Sans', sans-serif;
  box-shadow: 0 0 14px #ffb74d;
  max-width: 720px;
  margin: 30px auto;
}

.page-container.student-schedule h1 {
  color: #ef6c00;
  margin-bottom: 20px;
  font-weight: 700;
}

.page-container.student-schedule p {
  font-size: 18px;
  margin-bottom: 15px;
  color: #e65100;
}

.page-container.student-schedule ol {
  padding-left: 24px;
  font-size: 18px;
  color: #bf360c;
  line-height: 1.6;
  list-style-type: decimal-leading-zero;
}

.page-container.student-schedule ol li {
  margin-bottom: 12px;
}

.page-container.student-schedule .extra-point {
  font-weight: 700;
  color: #d84315;
  margin-left: 8px;
}
