.page-container.physical-health {
  background: #e8f5e9;
  padding: 30px 35px;
  border-radius: 10px;
  font-family: 'Merriweather', serif;
  box-shadow: 0 0 14px #81c784;
  max-width: 720px;
  margin: 30px auto;
}

.page-container.physical-health h1 {
  color: #2e7d32;
  margin-bottom: 20px;
  font-weight: 700;
}

.page-container.physical-health p {
  font-size: 18px;
  margin-bottom: 15px;
  color: #1b5e20;
}

.page-container.physical-health ul {
  padding-left: 20px;
  font-size: 18px;
  color: #33691e;
  line-height: 1.6;
  list-style-type: circle;
}

.page-container.physical-health ul li {
  margin-bottom: 12px;
}

.page-container.physical-health .extra-point {
  font-weight: 700;
  color: #558b2f;
  margin-right: 6px;
}
