.navbar {
  background: #1e3a8a;
  padding: 10px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.nav-logo {
  color: white;
  font-weight: bold;
  font-size: 20px;
  text-decoration: none;
}
.nav-links a {
  color: white;
  margin-left: 20px;
  text-decoration: none;
}
.nav-links a:hover {
  text-decoration: underline;
}
