.page-container.study-tips {
  background: #e3f2fd;
  padding: 30px 35px;
  border-radius: 10px;
  font-family: 'Roboto', sans-serif;
  box-shadow: 0 0 14px #64b5f6;
  max-width: 720px;
  margin: 30px auto;
}

.page-container.study-tips h1 {
  color: #0d47a1;
  margin-bottom: 20px;
  font-weight: 700;
}

.page-container.study-tips ul {
  list-style-type: square;
  padding-left: 24px;
  font-size: 18px;
  color: #1a237e;
  line-height: 1.6;
}

.page-container.study-tips ul li {
  margin-bottom: 12px;
}

.page-container.study-tips .extra-point {
  font-weight: 700;
  color: #1565c0;
  margin-right: 6px;
}
.study-page {
  padding: 20px;
  background-color: #f8f9fa;
  min-height: 100vh;
  font-family: Arial, sans-serif;
}


