.home {
  text-align: center;
  padding: 40px;
}
.home-buttons {
  display: flex;
  justify-content: center;
  gap: 20px;
  flex-wrap: wrap;
  margin-top: 30px;
}
.home-buttons button {
  padding: 10px 20px;
  font-size: 16px;
  background: #2563eb;
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
}
.home-buttons button:hover {
  background: #1d4ed8;
}
